.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
  box-shadow: 0 0 1px 0 black;
  margin: -7px;
  background: rgba(0, 0, 0, 0.2);
}
ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.link {
  box-shadow: 0 0 1px 0 black;
  color: white;
  padding: 5px 10px;
  border-radius: 2px;
  font-size: 18px;
  text-decoration: none;
  margin: 5px;
  background: rgba(0, 0, 0, 0.8);
}
