.todobox {
  display: flex;
  flex-direction: row;
  width: fit-content;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 1);
  margin: auto;
  margin-top: 20px;
  text-decoration: none;
  background: white;
  color: black;
}
.todobox p {
  padding: 10px;
}

.todoInput {
  display: flex;
  flex-direction: column;
  width: fit-content;
  padding: 10px;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 1);
  margin: auto;
  margin-top: 20px;
  background: rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}

.todoInput > * {
  margin: 8px;
  border: 0;
  border-radius: 3px;
  font-size: 18px;
  box-shadow: 0 0 2px rgba(0, 0, 0, 1);
  box-sizing: border-box;
  padding: 5px;
}

.todoInput h1 {
  font-size: 24px;
  box-shadow: none;
}
.todoInput > button {
  color: white;
  background: black;
  border: 0;
  padding: 5px 10px;
  border-radius: 5px;

  margin-bottom: -1px;
}

.btn {
  margin: 4px;
  margin-top: 10px;
  font-size: 15px;
  color: white;
  background: black;
  border: 0;
  padding: 5px 10px;
  border-radius: 5px;
  box-shadow: 0 0 1px rgba(0, 0, 0, 1);
  cursor: pointer;
}
